html {
  text-align: center;
  font-size: xx-large;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #282c34;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.btn {
  background: #78cceb;
  background-image: -webkit-linear-gradient(top, #78cceb, #4aaba6);
  background-image: -moz-linear-gradient(top, #78cceb, #4aaba6);
  background-image: -ms-linear-gradient(top, #78cceb, #4aaba6);
  background-image: -o-linear-gradient(top, #78cceb, #4aaba6);
  background-image: linear-gradient(to bottom, #78cceb, #4aaba6);
  -webkit-border-radius: 28;
  -moz-border-radius: 28;
  border-radius: 28px;
  font-family: Arial;
  color: #ffffff;
  font-size: 36px;
  padding: 31px 31px 31px 31px;
  text-decoration: none;
}

.btn:hover {
  background: #7a91d6;
  background-image: -webkit-linear-gradient(top, #7a91d6, #3498db);
  background-image: -moz-linear-gradient(top, #7a91d6, #3498db);
  background-image: -ms-linear-gradient(top, #7a91d6, #3498db);
  background-image: -o-linear-gradient(top, #7a91d6, #3498db);
  background-image: linear-gradient(to bottom, #7a91d6, #3498db);
  text-decoration: none;
}
